.total-box {
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
  padding-top: 5vw;
  height: 32vw;
  position: relative;
  overflow-y: hidden;
  overflow-x: scroll;
  display: flex;
}

.project-box {
  background-color: #28283c;
  width: 52.8vw;
  max-width: 528px;
  height: 29.7vw;
  max-height: 297px;
  border-radius: 20px;
  overflow: hidden;
  border: 2px solid #feffa6;
  margin-right: 5vw;
}

.explain-text {
  color: #fff;
  font-size: 1.5em;
  position: absolute;
}
