@keyframes animation1 {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes animation3 {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.92);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes animation2 {
  0% {
    transform: rotate(5deg);
    stroke-dashoffset: 326px;
  }
  50% {
    transform: rotate(-5deg);
    stroke-dashoffset: 0px;
  }
  100% {
    transform: rotate(5deg);
    stroke-dashoffset: 326px;
  }
}
@keyframes animation4 {
  0% {
    stroke-dashoffset: 50px;
  }
  50% {
    stroke-dashoffset: 0px;
  }
  100% {
    stroke-dashoffset: 50px;
  }
}
@keyframes animation5 {
  0% {
    transform: rotate(-1deg);
  }
  50% {
    transform: rotate(1deg);
  }
  100% {
    transform: rotate(-1deg);
  }
}

.circle-an {
  position: absolute;
  width: 40%;
  height: 50%;
  left: 55%;
  top: 15vh;
  /* top: 50px; */
  font-size: 4.7rem;
  letter-spacing: 8px;
  stroke-dasharray: 500px;
  animation: animation1 3s 0s ease infinite, animation2 4s 0s ease-out infinite;
  opacity: 0.3;
}

.star-an {
  position: absolute;
  width: 74%;
  left: 14%;
  top: 101vh;
  animation: animation3 2.5s 0s ease infinite, animation4 3.5s 0s ease-out infinite,
    animation5 3.5s 0s ease-out infinite;
  opacity: 0.8;
  z-index: -1;
}

.star-an2 {
  position: absolute;
  width: 74%;
  left: 14%;
  top: 191vh;
  animation: animation3 2.5s 0s ease infinite, animation4 3.5s 0s ease-out infinite,
    animation5 3.5s 0s ease-out infinite;
  opacity: 0.8;
  z-index: -1;
}
